.portfolio-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 15px;
  text-align: center;
}

.portfolio-category h3 {
  cursor: pointer;
  background-color: #f0f0f0;
  padding: 10px;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.portfolio-category h3:hover {
  background-color: #ddd;
}

.portfolio-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.portfolio-item img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
}
